$white: #fcfcfc;
$black: hsl(0, 0, 14%);

$text: #665d42;
$heading: #3e6a3c;

$light-brown: #e8a000;
$yello: #ffd643;
$green: #c9e6a6;
$blue: #719585;
$brown: #f6c758;

* {
    color: $text; }

body {
    background-color: $white;
    background: repeating-linear-gradient(30deg,$white,$white 30px,rgba($black, 0.05) 30px,rgba($black, 0.05) 31px);

    background-image: url("../footer.svg");
    background-repeat: no-repeat;
    background-position: center bottom; }

h1 a, h2, h3 {
    color: $heading;
    text-decoration: none; }

.title {
    color: $white; }

main, footer {
    border-left: 1px solid rgba(0,0,0,0.05);
    border-right: 1px solid rgba(0,0,0,0.05); }

header {
    h1, nav .menu, .menu-icon {
        background: $white;
        @include desktop {
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px; } } }

main {
    background: rgba($white, 0.97); }

footer {
    .title, a, li, p {
        color: $white;
        text-shadow: rgba(0,0,0,0.1); } }

a {
    color: $black; }
